import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { getCookie } from "../../../wrappers/cookies"

// Styles
import styles from './index.module.scss'

// Images
import userAvatar from '../../../images/user.jpg'

// Header User
export default props => {
    const [isOpen, setIsOpen] = useState(false)

    const username = getCookie('userName')

    return (
        <div className={styles.headerUser}>
            <Link className={styles.headerUserPhoto} to='/'>
                <img src={userAvatar} alt="avatar"/>
            </Link>
            <div className={`${styles.headerUserName} ${isOpen && styles.headerUserNameActive}`} onClick={() => setIsOpen(!isOpen)}>
                {
                    username ? username.length > 15 ? username.slice(0, 15) + '...' : username : 'Hidden user @_@'
                }
                <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px"
                     width="451.847px" height="451.847px" viewBox="0 0 451.847 451.847">
                    <path d="M225.923,354.706c-8.098,0-16.195-3.092-22.369-9.263L9.27,151.157c-12.359-12.359-12.359-32.397,0-44.751
                        c12.354-12.354,32.388-12.354,44.748,0l171.905,171.915l171.906-171.909c12.359-12.354,32.391-12.354,44.744,0
                        c12.365,12.354,12.365,32.392,0,44.751L248.292,345.449C242.115,351.621,234.018,354.706,225.923,354.706z"/>
                </svg>
            </div>
            <div className={`${styles.headerUserActions} ${isOpen && styles.headerUserActionsActive}`}>
                <ul>
                   <li className={styles.headerUserItem}>
                      <Link to='/'>Profile</Link>
                   </li>
                   <li className={styles.headerUserItem}>
                      <button onClick={props.logout}>Logout</button>
                   </li>
                </ul>
            </div>
        </div>
    )
}